
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import { assetApis } from '../apis/urls'

export default {
data() {
return {
code: '',
name: '',
band: '',
model: '',
imei: '',
deptName: '',
accountName: '',
position: '',
describe: ''
}
},
components: {
Box,
Group,
XInput,
XNumber,
XTextarea,
XButton,
Toast,
assetApis
},
methods: {

getAssetByCode() {
let _this = this;
if(_this.code) {










this.$http.get('/WeixinApi/GetAssetByCode', {
params: {
code: _this.code
}
}).then(function(response) {
if(response.code == 200) {
_this.setForm(response.body)
} else {
_this.clearForm()
console.log(response.message)
}
}).catch(function(error) {
console.log(error);
});
} else {
_this.clearForm()
}
},

submitForm() {},

clearForm() {
let _this = this;
_this.code = ''
_this.name = ''
_this.band = ''
_this.model = ''
_this.imei = ''
_this.deptName = ''
_this.accountName = ''
_this.position = ''
},
setForm(asset) {
let _this = this;
if(!asset) {
clearForm()
} else {
_this.code = asset.code
_this.name = asset.name
_this.band = asset.band
_this.model = asset.model
_this.imei = asset.imei
_this.deptName = asset.deptName
_this.accountName = asset.accountName
_this.position = asset.position
}
},
showScaner() {
Vue.wechat.scanQRCode()
},
getData() {
this.$http.get('../apis/data.json')
.then((data) => {
console.log(data.data)
}).catch((request) => {
console.log(request)
})
}
}
}



